import {
	Button,
	Grid,
	Stack,
	TextField,
	Divider,
	CardContent,
	CardActions,
} from '@material-ui/core';
import { FormikHelpers, useFormik } from 'formik';
import _get from 'lodash.get';
import { generatePath, useHistory } from 'react-router-dom';
import { useMutation, gql } from '@apollo/client';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { gridSpacing } from '../../../store/constant';
import InputLabel from '../../../ui-component/extended/Form/InputLabel';
import { TAddProperty } from '../../../types';
import { createProperty } from '../../../graphql/mutations';
import { validationAddNewSchema } from './Validation';
import { useSnackBar } from '../../../hooks';
import { PATHS } from '../../../routes/path';
import 'react-phone-number-input/style.css';
import { PhoneInput } from '../components/PhoneInput';
import config from '../../../config';

const PropertyAdd = () => {
	const history = useHistory();
	const [addNewProperty] = useMutation<TAddProperty>(gql(createProperty));
	const showSnackBar = useSnackBar();
	const onSubmitHandler = async (
		values: TAddProperty,
		{ setStatus, setSubmitting }: FormikHelpers<TAddProperty>
	) => {
		try {
			const { email, firstName, lastName, phone } = values;
			const variables = {
				SellerInfo: {
					Email: email,
					FirstName: firstName,
					LastName: lastName,
					Phone: phone || undefined,
				},
			};
			const { data } = await addNewProperty({
				variables,
			});
			showSnackBar({ message: 'Account created', alertSeverity: 'success' });
			const propertyId = _get(data, ['createProperty', 'PropertyId']);
			const propertyLink = generatePath(PATHS.PROPERTY, {
				id: propertyId,
			});
			history.push(propertyLink);
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({ message });
		}
	};

	const formik = useFormik({
		initialValues: {
			email: '',
			firstName: '',
			lastName: '',
		},
		validationSchema: validationAddNewSchema,
		onSubmit: onSubmitHandler,
	});

	const {
		touched,
		errors,
		handleSubmit,
		values,
		handleChange,
		handleBlur,
		isSubmitting,
		setFieldValue,
	} = formik;

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item xs={12}>
				<form noValidate onSubmit={handleSubmit}>
					<CardContent>
						<Grid container spacing={2} alignItems='start'>
							<Grid item xs={12} lg={6}>
								<InputLabel>First Name</InputLabel>
								<TextField
									defaultValue={values.firstName}
									fullWidth
									placeholder='First Name'
									onChange={handleChange}
									onBlur={handleBlur}
									error={touched.firstName && Boolean(errors.firstName)}
									helperText={touched.firstName && errors.firstName}
									name='firstName'
								/>
							</Grid>
							<Grid item xs={12} lg={6}>
								<InputLabel>Last Name</InputLabel>
								<TextField
									defaultValue={values.lastName}
									fullWidth
									placeholder='Last Name'
									onChange={handleChange}
									onBlur={handleBlur}
									error={touched.lastName && Boolean(errors.lastName)}
									helperText={touched.lastName && errors.lastName}
									name='lastName'
								/>
							</Grid>
							<Grid item xs={12} lg={6}>
								<InputLabel>Email</InputLabel>
								<TextField
									defaultValue={values.email}
									fullWidth
									placeholder='Email'
									onChange={handleChange}
									onBlur={handleBlur}
									error={touched.email && Boolean(errors.email)}
									helperText={touched.email && errors.email}
									name='email'
								/>
							</Grid>
							<Grid item xs={12} lg={6}>
								<InputLabel>Phone</InputLabel>
								<PhoneInput
									country={config.locate.countryCode}
									placeholder=''
									value={values.phone}
									onChange={(value: string) => setFieldValue('phone', value)}
									onBlur={handleBlur}
									name='phone'
									error={touched.phone && errors.phone}
								/>
							</Grid>
						</Grid>
					</CardContent>
					<Divider />
					<CardActions>
						<Grid item xs={12}>
							<Stack direction='row' justifyContent='flex-end'>
								<AnimateButton>
									<Button
										color='primary'
										variant='contained'
										type='submit'
										disabled={isSubmitting}
									>
										Save
									</Button>
								</AnimateButton>
							</Stack>
						</Grid>
					</CardActions>
				</form>
			</Grid>
		</Grid>
	);
};

export default PropertyAdd;
