import { FormHelperText, Theme } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import clsx from 'clsx';
import PhoneNumberInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';

const useStyle = makeStyles((theme: Theme) => ({
	wrapper: {
		background: theme.palette.grey['50'],
		borderRadius: '12px',
		border: '1px solid rgba(0, 0, 0, 0.23)',
		'&& .selected-flag, &&& .selected-flag.open': {
			width: '40px',
			borderRadius: '12px 0 0 12px',
			borderWidth: '0 1px 0 0',
		},
		'&& .flag-dropdown, && .flag-dropdown.open': {
			borderRadius: '12px 0 0 12px',
			borderWidth: '0 1px 0 0',
		},
	},
	phoneInput: {
		'&&&': {
			padding: '14.5px 14px 14.5px 48px',
			height: 'auto',
			width: '100%',
			outline: 'none',
			border: 0,
			background: 'transparent',
			fontSize: '0.875rem',
			lineHeight: '20px',
			color: theme.palette.grey['900'],
			fontWeight: 500,
			fontFamily: theme.typography.fontFamily,
		},
	},
	error: {
		borderColor: theme.palette.error.main,
	},
	helper: {
		fontSize: '0.75rem',
		margin: '3px 14px 0',
	},
}));

type Props = {
	name: string;
	value: string | undefined;
	onChange: (value: string) => void;
	onBlur: (e: FocusEvent) => void;
	error: string | boolean | undefined;
	country?: string;
	placeholder?: string;
};

export const PhoneInput = (props: Props) => {
	const classes = useStyle();

	const {
		name,
		value,
		onChange,
		onBlur,
		error,
		country = '',
		placeholder = '',
	} = props;

	return (
		<>
			<div
				tabIndex={-1}
				className={clsx('MuiOutlinedInput-input', classes.wrapper, {
					[classes.error]: error,
				})}
			>
				<PhoneNumberInput
					country={country.toLowerCase()}
					placeholder={placeholder}
					value={value}
					onChange={onChange}
					inputProps={{
						className: clsx('form-control', classes.phoneInput),
						onBlur,
						name,
					}}
				/>
			</div>
			{error ? (
				<FormHelperText className={classes.helper} error>
					{error}
				</FormHelperText>
			) : null}
		</>
	);
};
